.details {
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: auto;
    margin-top: 50px;
  }
  
  .details > div > .details_text {
    height: calc(103px / 2);
  }
  
  .details > div > .details_img > img {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  
  .details > div > .details_text > div {
    width: 100%;
    border-top: 2px solid black;
  }
  
  .details > div > .details_text > .details_name {
    height: 40%;
  }
  
  .details > div > .details_text > .details_more {
    height: 60%;
  }
  
  .details > div > .details_img {
    width: 100%;
    height: calc(340px / 2);
    background-color: white;
  }
  
  .details > div {
    width: calc(350px / 2);
    height: calc(450px / 2);
    margin: 11px;
    border: 2px solid black;
  }
  
  .details > div > .details_text > .details_more > .btn {
    height: calc(44px / 2);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: calc(30px / 2);
  }
  
  h3 {
    text-align: center;
    font-size: calc(29px / 2);
  }
  @media (max-width: 450px) {
    .details > div > .details_text {
      height: calc(103px / 3);
    }
    .details > div > .details_img {
      height: calc(340px / 3);
    }
    .details > div {
      width: calc(350px / 3);
      height: calc(450px / 3);
      margin: 11px;
    }
    .details > div > .details_text > .details_more > .btn {
      height: calc(44px / 3);
      font-size: calc(30px / 3);
    }
    h3 {
      font-size: calc(29px / 3);
    }
  }
  @media (min-width: 1300px) {
    .details > div > .details_text {
      height: calc(103px / 1.5);
    }
    .details > div > .details_img {
      height: calc(340px / 1.5);
    }
    .details > div {
      width: calc(350px / 1.5);
      height: calc(450px / 1.5);
      margin: 22px;
    }
    .details > div > .details_text > .details_more > .btn {
      height: calc(44px / 1.5);
      font-size: calc(30px / 1.5);
    }
    h3 {
      font-size: calc(29px / 1.5);
    }
  }
  