.footer {
    height: 160px;
    width: 100%;
    background-color: #081a39;
    display: flex;
    justify-content: center;
    align-items: center;
}

.date {
    height: 90%;
    width: 300px;
}

.icon_div {
    display: flex;
    justify-content: space-between;
}

.icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    background-color: white;
}
i{
    font-size: 60px;
    margin-left: 1px;
}
.footer-detal-text,.footer-detal{
    display: flex;
    margin-top: 10px;
    justify-content: center;
    align-items: center;
    color: white;
}
.footer-detal-text{
    color: rgba(229, 228, 228, 0.808);
}
@media (max-width:450px) {
    .footer {
        height:calc(160px/1.2);
    }
    .footer-detal-text,.footer-detal{
        margin-top:calc(10px/1.2);  
    }
    .icon {
        width:calc(65px/1.2);
        height:calc(65px/1.2) ;
    }
    i{
        font-size:calc(60px/1.2);
    }
    .date {
        height:calc( 90%/1.2);
        width:calc(300px/1.2);
    } 
}